@import "../../../../variables";

.checkout-container {
  width: 400px;
  min-height: 400px;

  .checkout-products-container {
    display: flex;
    flex-direction: column;
    gap: 10px;
    max-height: 400px;
    overflow: auto;

    .checkout-products-wrapper {
      background-color: $off-white;
      display: flex;
      align-items: center;
      gap: 20px;
      padding: 10px;
      .checkout-product-image {
        width: 70px;
        height: 70px;

        img {
          width: 100%;
          height: 100%;
        }
      }

      .checkout-product-name {
        width: 150px;
      }

      .checkout-product-quantity {
        color: $gray;
        font-size: 14px;
      }

      .checkout-product-price {
        color: $green;
        font-weight: 600;
      }
    }
  }

  .checkout-price-part {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    background-color: $off-white;
    padding: 5px 10px;

    .coupon {
      font-weight: 400;
      font-size: 14px;
      line-height: 16px;
    }
    .coupon-input {
      display: flex;
      input {
        width: 332px;
        height: 35px;
        border-radius: 3px;
        padding-left: 15px;
        border: 2px solid $green;
      }
      button {
        width: 100px;
        height: 40.2px;
        margin-left: -20px;
        border: none;
        background-color: $green;
        color: $white;
        cursor: pointer;
        transition: 0.3s ease;

        &:hover {
          background-color: darken($green, 10%);
        }
      }
    }
  }
  .price-part {
    display: flex;
    justify-content: space-between;
  }

  .total-price {
    margin-top: 30px;

    .total-price-value {
      color: $green;
      font-weight: 600;
    }
  }

  .payment-container {
    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 320px;

    .payment-method {
      border: 1px solid $light-green;
      width: 100%;
      height: 45px;
      display: flex;
      align-items: center;
      gap: 30px;
      img {
        width: 230px;
        height: 30px;
      }
    }
  }
  .card-details {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 40px;

    input {
      width: 200px;
      height: 20px;
    }
  }
}
